import React, { useState, useEffect } from 'react';
import Head from 'next/head';
import AdSense from 'react-adsense';

import { getRecentArticles } from '../services/utils';
import { adClientId } from '../constants/constants';
import NavBar from '../components/NavBar';
import RecentArticles from '../components/RecentArticles';
import Footer from '../components/Footer';
import { getArticleUrl } from '../services/utils';

import LandingPageContainer from '../styles/LandingPage.style';

export default function Index() {
  const [recent, setRecent] = useState([]);

  useEffect(() => {
    getRecentArticles()
      .then((res) => {
        if (res) setRecent(res.data && res.data.length ? res.data : []);
      })
      .catch(() => {
      });
  }, []);

  const url = getArticleUrl('aishu');

  return (
    <>
      <NavBar />
      <Head>
        <title>Space Science</title>
        <meta name="description" content="Lets explore space and science" />

        <meta itemProp="name" content="Space Science" />
        <meta itemProp="description" content="Lets explore space and science" />
      </Head>
      <LandingPageContainer>
        <div className="landing_page__container">
          <div className="container">
            <div className="row">
              <div className="col-12">
                <h1 className="welcome-text">
                  Space Science
              </h1>
                <p className="lead">
                  Lets explore some fascinating things about space
                {' '}
                  <br />
                and see how science helps us uncover them
              </p>
                <p className="lead">
                  Aishu click
                  <Link href={url}>
                    <a rel="noopener">
                      here
                    </a>
                  </Link>
                </p>
              </div>
              <div className="col-12">
                <hr />
              </div>
              <div className="col-12">
                <AdSense.Google
                  client={adClientId}
                  slot="7013353577"
                  format="auto"
                  responsive="true"
                />
              </div>
            </div>
            <div className="recent">
              You might be interested in:
          </div>
            <RecentArticles articles={recent} />
          </div>
        </div>
      </LandingPageContainer>
      <Footer />
    </>
  );
}
